@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700&display=swap);
  table tbody{display: none;background-color:none!important;} 
  table{background-color:none!important;color:#EBEDEF; padding: 0 16px;border-radius: 5px;}
  table thead tr th{display: none;}
  table tbody tr, table tbody th{background-color: #EEEEEE ;}
  table tbody,table tbody tr{display: block;width: 100%;}
  table tbody tr td::before{content: attr(data-title);display: block;font-weight: bold;font-size: 18px;}
  table tbody tr td,
  table tbody tr th{display: block;width: 50%;float: left;color:#E67E22;font-weight: bold;font-size: 15px;}
  table td, table th{border-top: 2px solid #dee2e6;padding: 0 16px;}
  table tbody tr td a{margin-top: 2px;}
  table tbody tr{margin-bottom: 16px;float:left;clear:both;box-shadow: 0px 4px 20px #EF6C00;border-radius: 15px;
  -moz-box-shadow:0px 4px 20px #EF6C00;
  -webkit-box-shadow:0px 4px 20px #EF6C00;}
  


html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.home-page,.customer-page,.transaction-page,.transfer-form
{
  min-height:100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: linear-gradient(90deg, #F1C40F 50%, #D5D8DC  50%);
}

.home-page p,.home-page h1
{
  text-align: center;
  color:#283747;
}



.transfer-form ul
{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.transfer-form li,.transaction-page li ,.customer-page li
{
  background: none;
  border:1px solid #F39C12;
}
form
{
    border-radius: 50px 0px 50px 0px;
    -webkit-flex-direction: column;
            flex-direction: column;
    border: 1px solid white;
    box-shadow: 0px 10px 20px #212F3C;
    padding: 20px 20px;
    background-image: linear-gradient(to right, rgba(231, 189, 52, 0.938), rgba(240, 163, 49, 0.575));
    margin-top: 5px;
    margin-bottom: 20px;
}
form input {
  text-align: center;
  text-transform: capitalize;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 5px;
  color: #F39C12;
  border: 1px solid white;
  border-radius: 0px 20px 0px 20px;
  padding: 10px 10px;
  outline: none;
  background:none;
}
#from,#to
{
  border: 1px solid #fff;
  border-radius: 15px;
  outline: none;
  text-align: center;
  padding: 5px;
  width: 205px;
  background: #F39C12;
  color: #fff;
}
#from  option,#to option
{
  background: #fff;
  color: #F39C12;
  border:1px solid #F39C12;
  border-radius: 4px;
}


form button
{
  background: #F57C00;
  border: 1px solid white;
  border-color: #fff;
  border-radius: 20px;
  padding: 6px 12px;
  box-shadow: 0px 4px 20px #FFEB3B;
  cursor: pointer;
  color: #fff;
  outline: none;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.65, -0.25, 0.25, 1.95);
}
.box
{
  box-shadow:  4px 4px #99A3A4;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
  border-radius: 5px;
  background: #F39C12;
  color:#fff;
}
.customer-page li{
  background: none;
}
.Loading
{
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  background:#283747;
}

